<template>
<div>
  <h3 class="page-header">Menu</h3>
  <div class="expander">
    <div
      :class="startersVisible ? null : 'collapsed'"
      :aria-expanded="startersVisible ? 'true' : 'false'"
      aria-controls="starters"
      @click="startersVisible = !startersVisible"
      class="expander-header">
      <h5>Starters</h5>
      <div v-if="!startersVisible" class="expander-status">
        <strong>&#43;</strong>
      </div>
      <div v-else class="expander-status">
        <strong>&#8722;</strong>
      </div>
    </div>
    <b-collapse id="starters" v-model="startersVisible">
      <div class="row">
        <template v-for="(item, index) in starters">
          <app-menu-item
            :key="item.name"
            :index="index"
            :name="item.name"
            :desc="item.desc"
            :price="item.price"
            :imageName="item.imageName"
            :marked="item.marked"></app-menu-item>
        </template>
      </div>
    </b-collapse>
  </div>
  <div class="expander">
    <div
      :class="mainCourseVisible ? null : 'collapsed'"
      :aria-expanded="mainCourseVisible ? 'true' : 'false'"
      aria-controls="main-course"
      @click="mainCourseVisible = !mainCourseVisible"
      class="expander-header">
      <h5>Main Courses</h5>
      <div v-if="!mainCourseVisible" class="expander-status">
        <strong>&#43;</strong>
      </div>
      <div v-else class="expander-status">
        <strong>&#8722;</strong>
      </div>
    </div>
    <b-collapse id="main-course" v-model="mainCourseVisible">
      <div class="row">
        <template v-for="(item, index) in mainCourses">
          <app-menu-item
            :key="item.name"
            :index="index"
            :name="item.name"
            :desc="item.desc"
            :price="item.price"
            :imageName="item.imageName"
            :marked="item.marked"></app-menu-item>
        </template>
      </div>
    </b-collapse>
  </div>
  <div class="expander">
    <div
      :class="dessertsVisible ? null : 'collapsed'"
      :aria-expanded="dessertsVisible ? 'true' : 'false'"
      aria-controls="dessert"
      @click="dessertsVisible = !dessertsVisible"
      class="expander-header">
      <h5>Desserts</h5>
      <div v-if="!dessertsVisible" class="expander-status">
        <strong>&#43;</strong>
      </div>
      <div v-else class="expander-status">
        <strong>&#8722;</strong>
      </div>
    </div>
    <b-collapse id="dessert" v-model="dessertsVisible">
      <div class="row">
        <template v-for="(item, index) in desserts">
          <app-menu-item
            :key="item.name"
            :index="index"
            :name="item.name"
            :desc="item.desc"
            :price="item.price"
            :imageName="item.imageName"></app-menu-item>
        </template>
      </div>
    </b-collapse>
  </div>
  <div class="expander">
    <div
      :class="drinksVisible ? null : 'collapsed'"
      :aria-expanded="drinksVisible ? 'true' : 'false'"
      aria-controls="drinks"
      @click="drinksVisible = !drinksVisible"
      class="expander-header">
      <h5>Cocktails</h5>
      <p class="expander-subtitle">(Full bar available)</p>
      <div v-if="!drinksVisible" class="expander-status">
        <strong>&#43;</strong>
      </div>
      <div v-else class="expander-status">
        <strong>&#8722;</strong>
      </div>
    </div>
    <b-collapse id="drinks" v-model="drinksVisible">
      <div class="row">
        <template v-for="(item, index) in drinks">
          <app-menu-item
            :key="item.name"
            :index="index"
            :name="item.name"
            :desc="item.desc"
            :price="item.price"
            :imageName="item.imageName"></app-menu-item>
        </template>
      </div>
    </b-collapse>
  </div>
  <div class="expander">
    <div
      :class="winesVisible ? null : 'collapsed'"
      :aria-expanded="winesVisible ? 'true' : 'false'"
      aria-controls="wines"
      @click="winesVisible = !winesVisible"
      class="expander-header">
      <h5>Wine List</h5>
      <div v-if="!winesVisible" class="expander-status">
        <strong>&#43;</strong>
      </div>
      <div v-else class="expander-status">
        <strong>&#8722;</strong>
      </div>
    </div>
    <b-collapse id="wines" v-model="winesVisible">
      <h5 class="wine-type">White</h5>
      <div class="row">
        <template v-for="(item, index) in wineListWhite">
          <app-menu-item
            :key="item.name"
            :index="index"
            :name="item.name"
            :desc="item.desc"
            :price="item.price"
            :bottlePrice="item.bottlePrice"
            :type="item.type"
            :location="item.location"
            :imageName="item.imageName"></app-menu-item>
        </template>
      </div>
      <h5 class="wine-type">Red</h5>
      <div class="row">
        <template v-for="(item, index) in wineListRed">
          <app-menu-item
            :key="item.name"
            :index="index"
            :name="item.name"
            :desc="item.desc"
            :price="item.price"
            :bottlePrice="item.bottlePrice"
            :type="item.type"
            :location="item.location"
            :imageName="item.imageName"></app-menu-item>
        </template>
      </div>
      <h5 class="wine-type">Sparkling</h5>
      <div class="row">
        <template v-for="(item, index) in wineListSparkling">
          <app-menu-item
            :key="item.name"
            :index="index"
            :name="item.name"
            :desc="item.desc"
            :price="item.price"
            :bottlePrice="item.bottlePrice"
            :type="item.type"
            :location="item.location"
            :imageName="item.imageName"></app-menu-item>
        </template>
      </div>
    </b-collapse>
  </div>
  <div class="disclaimer">
    <div>
      <strong class="star">&#42;</strong>
      <p>This menu item can be cooked to you liking.</p>
    </div>
    <div>
      <strong class="star">&#42;</strong>
      <p>Consuming raw or undercooked meat, fish, shellfish or fresh shell eggs may increase your risk of food borne illness, especially if you have certain medical conditions.</p>
    </div>
    <p>Before placing your order, please inform your server if a person in your party has a food allergy.</p>
    <p>Menu Items and prices subject to change.</p>
  </div>
</div>
</template>

<script>
import { dataStarters,
  dataMainCourses,
  dataDesserts,
  dataDrinks,
  dataWineListWhite,
  dataWineListRed,
  dataWineListSparkling } from '../data'
import MenuItem from './MenuItem'
export default {
  components: {
    'app-menu-item': MenuItem
  },
  name: 'Menu',
  metaInfo: {
    title: 'Red Rooster Bistro',
    titleTemplate: '%s | Menu'
  },
  data: function () {
    // upload menu images in a 4:3 (width:height) ratio
    return {
      startersVisible: false,
      mainCourseVisible: false,
      dessertsVisible: false,
      drinksVisible: false,
      winesVisible: false,

      starters: dataStarters,
      mainCourses: dataMainCourses,
      desserts: dataDesserts,
      drinks: dataDrinks,
      wineListWhite: dataWineListWhite,
      wineListRed: dataWineListRed,
      wineListSparkling: dataWineListSparkling
    }
  }
}
</script>

<style scoped>
.disclaimer {
  font-size: 0.8em;
  text-align: center;
  margin-top: 40px
}
.disclaimer > div {
  display: flex;
  justify-content: center;
}

.menu-space {
  height: 200px;
}

.page-header {
  margin-bottom: 40px;
}

.expander {
  margin-bottom: 20px;
}

.expander-header {
  background-color: white;
  height: 50px;
  box-shadow: 0px 0px 3px #888888;
  display: flex;
  justify-content: center;
  align-items: center;
  justify-content: space-between;
  cursor: pointer;
}

.expander-header > h5 {
  margin: 0;
  margin-left: 30px;
}

.expander-subtitle {
  margin: 0;
  font-size: 0.9em;
  color: #8a0707;
}

.expander-status {
  margin-right: 30px;
  font-size: 2em;
  user-select: none;
}

.star {
  color: #8a0707;
  font-size: 1.6em;
  margin-right: 5px;
}

.wine-type {
  width: fit-content;
  margin-left: 30px;
  margin-top: 30px;
  margin-bottom: 0;
  font-size: 1.5em;
  font-weight: bold;
}
</style>



